@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Gudea&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Gudea&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.home-content{
    display: flex;
    justify-content: center;
    text-align: center;
    margin-top: 40vh;
    font-family: 'Open Sans Condensed', sans-serif;
    font-size: 20px;
    margin-bottom: 100px;
}

.home-logo{
    font-size: 40px;
    font-family: 'Gudea', sans-serif;

}

.conteiner{
    display: flex;
    justify-content: center;
    align-items: center;
    justify-self: center;
    align-self: center;
    flex-direction: column;
    margin: auto;
    margin-top: 30vh;
    width: 350px;
    height: 300px;
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0.105);
    box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.256);
    text-align: center;
    padding: 10px;
}

.input{
    margin-top: 10px;
    display: flex;
}

#title{
    font-size: 35px;
    margin: 5px;
    
}

.btn-entry{
    text-align: center;
    padding: 10px;
    margin: 10px;
    width: 50%;
    color: white;
    background-color: rgb(62, 62, 255);
    border-radius: 5px;
    border: none;
}

.msg-error{
    color: red;
    text-align: center;
}

.create-page{
    margin-top: 120px;
}

input{
    width: 99%;
    margin-left: 5px;
}

textarea{
    width: 100% !important;
}

.create-area{
    width: 70%;
    margin: auto;
    text-align: center;
}

#editor{
    margin-top: 20px;
}

.render-post{
    font-size: 25px;
}

.nav-bar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: 'Gudea', sans-serif;
    font-size: 30px;
    height: 100px;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.144);
    position: fixed; 
    top: 0;
}

.right{
    display: flex;
    margin-right: 20px;
    font-size: 15px;
}


a{
    padding-left: 15px;
    padding-right: 5px;
    color: black;
    text-decoration: none;
}

.card-conteiner{
    width: 250px;
    border: 1px solid rgba(0, 0, 0, 0.14);
    box-shadow: 4px 4px 2px rgba(77, 77, 77, 0.098);
    margin: 20px;
    margin: 30px;
    padding: 20px;
    
}

.publi-conteiner{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 100px;
}

#title-card{
    font-size: 25px;
    overflow: hidden;
    text-overflow: ellipsis
}

#author-card{
    padding-top: 10px;
    font-size: 10px;
}

.logo{
    display: flex;
    align-items: center;
}


